'use client'

import { navItems } from '@/constants'
import Image from 'next/image'
import Link from 'next/link'
import { usePathname } from 'next/navigation'
import React from 'react'

const Home = () => {

    const pathname = usePathname()

    // const [isActive, setIsActive] = useState<boolean>(false);
    // const [isActiveState, setIsActiveState] = useState<boolean>(false);
    // // const [isScrolled, setIsScrolled] = useState<boolean>(false);

    // const toggleMenu = () => setIsActive((prev) => !prev);

    console.log(pathname)

    return (
        <div className="attach-image">
            <header
                className={`w-full h-[80px]
                     mx-auto sticky top-0 z-10 flex justify-between items-center transition-all duration-300 
                `}
            // ${isScrolled ? 'lg:max-w-[80%] lg:top-4 lg:backdrop-blur-sm lg:rounded-2xl' : 'lg:w-full'}
            >
                <div className="container w-full flex justify-between items-center">
                    <Link href="/">
                        <Image
                            src="/assets/home/site-logo.svg"
                            alt="PollPe Form Logo"
                            className="w-32 h-auto"
                            width={32}
                            height={20}
                        />
                    </Link>
                    <nav className="md:flex flex-1 justify-center items-center space-x-6">
                        {navItems.map(({ name, href }, index) => (
                            <Link key={index} href={href} className={` hover:text-[#34a0a4] 
                                ${pathname === href ? "text-[#34a0a4]" : "text-white"}
                                `}>{name}</Link>
                        ))}
                    </nav>
                </div>
            </header>
        </div>
    )
}

export default Home